* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.products {
    max-width: 640px;
    width: 100%;
    margin: 56px auto;
    display: grid;
    grid-template-columns: 33% 33% 33%;
    grid-column-gap: 1%;
}

.product {
    &__image {
        width: 100%;
    }
    &__name {
        font-size: 1.2em;
        margin: 8px 0;
    } 
    &__description {
        color: gray;
    }
}

.is--highlight {
    border: 2px solid gold;
    padding: 8px;
    background-color: #ffff42;
}